// BASIC SETUP

* {
    box-sizing: border-box;
}

body,
html {
    padding: 0;
    margin: 0;
    font-family: "Raleway", sans-serif;
    color: #404040;
    scroll-behavior: smooth;
}

// HOME

.home__container {
    background-color: #e6d3a3;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 100vh;

    &__header {
        font-size: 600%;
        margin-bottom: 0;

        span {
            color: #91972a;
        }

        @media (max-width: 800px) {
            font-size: 450%;
            padding: 4rem;
        }

        @media (max-width: 425px) {
            margin-right: 6rem;
        }

        @media (max-width: 360px) {
            font-size: 350%;
            padding-left: 3rem;
        }
    }

    &__slogan {
        font-size: 160%;
        color: #404040;

        span {
            color: #91972a;

            // @media (max-width: 800px) {
            //     color: #a3ceff;
            // }
        }
        @media (max-width: 800px) {
            font-size: 140%;
        }

        @media (max-width: 500px) {
            font-size: 130%;
            width: 85%;
        }
    }
}

// NAV

.main_nav {
    position: fixed;
    top: 1.5rem;
    right: 0;
    z-index: 2;

    a {
        font-size: 120%;
        text-decoration: none;
        padding: 2rem;
        color: #91972a;

        @media (max-width: 560px) {
            font-size: 90%;
            padding: 0.75rem;
            border-radius: 1.2rem;
        }
    }

    a:hover {
        color: #fffef9;
        transition: color 0.2s;
    }
}

// SEARCH BAR

.filter {
    margin-top: 3em;
    padding: 0.5em;
    display: flex;
    justify-content: center;
    background-color: #91972a;
    border-radius: 1.2rem;

    @media (max-width: 360px) {
        margin-left: 2rem;
    }

    @media (max-width: 410px) {
        margin-right: 5rem;
    }
}

.filter__input {
    padding: 1em;
    outline: none;
    border-style: none;
    border-radius: 0.75rem;
    font-size: 120%;
}

.list {
    display: flex;
    flex-direction: column;
    color: #404040;
    padding: 1em;
    border-bottom: 1px solid #91972a;
}

.country-select {
    font-size: 1.75rem;
    font-weight: bold;
}

.country-select:hover {
    cursor: pointer;
    color: #91972a;
}

// HERO

.hero__container {
    height: 100vh;
    background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
        url("../img/kitchen.jpg");
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #e6d3a3;

    blockquote {
        font-size: 200%;
        font-style: italic;
        padding: 1rem;
        border-radius: 0.75rem;

        @media (max-width: 880px) {
            font-size: 120%;
        }
        @media (max-width: 500px) {
            font-size: 80%;
        }
        @media (max-width: 360px) {
            padding: 0.5rem;
        }
    }

    cite {
        display: inline-block;
        margin-left: 4rem;

        @media (max-width: 360px) {
            font-size: 80%;
            padding: 0.5rem;
        }
    }
}

// COUNTRY RESULTS

.country {
    background-color: #f2f4f3;

    &__container {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    &__heading {
        color: #91972a;
        font-size: 300%;
        margin-top: 5rem;

        @media (max-width: 530px) {
            font-size: 200%;
            align-items: center;
        }
    }

    &__tag {
        font-size: 120%;
        @media (max-width: 530px) {
            font-size: 100%;
        }
    }

    &__options {
        text-align: center;
        margin-top: 4rem;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-evenly;

        @media (max-width: 1030px) {
            flex-direction: column;
            align-items: center;
        }

        @media (max-width: 350px) {
            font-size: 80%;
        }
    }

    &__option {
        flex-grow: 1;
        min-width: 33%;
        color: #91972a;
        margin-bottom: 4rem;
        font-size: 90%;
        @media (max-width: 530px) {
            margin-top: 1.5rem;
        }

        img {
            width: 200px;
            height: 150px;
            padding: 0.5rem;
            border-radius: 1.5rem;
            transform: scale(1.15);
            transition: transform 0.5s, opacity 0.5s;

            @media (max-width: 350px) {
                width: 200px;
                height: 150px;
            }
        }
        img:hover {
            opacity: 1;
            transform: scale(1.03);
            cursor: pointer;
        }
    }
}

// NO RESULTS

.no_results {
    background-color: #919720;
    color: #e3f0ff;
    padding: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    h2 {
        @media (max-width: 800px) {
            font-size: 100%;
        }
        @media (max-width: 570px) {
            font-size: 80%;
        }
        @media (max-width: 460px) {
            text-align: center;
        }
    }

    p {
        text-align: center;
    }
}

// RECIPE RESULTS

.recipe {
    background-color: #d8d174;

    &__container {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    &__heading {
        font-size: 300%;
        margin-top: 5rem;
        text-align: center;
    }

    &__ingredients {
        display: flex;
        flex-direction: row;
        justify-content: space-between;

        @media (max-width: 780px) {
            flex-direction: column;
        }
    }

    &__ingredient {
        padding: 1.5rem;

        img {
            border-radius: 3rem;
            width: 300px;
        }
        img:hover {
            transform: scale(1.2);
            cursor: pointer;
        }
    }

    &__instructions {
        margin: 2rem 0;
        width: 60%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__bon {
        margin-bottom: 3rem;
    }
}

// Footer

.footer {
    background-color: #404040;
    padding: 50px;
    font-size: 80%;

    &__nav {
        list-style: none;
        float: left;
    }

    &__links {
        list-style: none;
        float: right;
    }

    &__nav li,
    &__links li {
        display: inline-block;
        margin-right: 20px;
    }

    &__nav li:last-child,
    &__links li:last-child {
        margin-right: 0;
    }

    &__nav li a:link,
    &__nav li a:visited,
    &__links li a:link,
    &__links li a:visited {
        text-decoration: none;
        border: 0;
        color: #d8d174;
        transition: color 0.2s;
    }

    &__nav li a:hover,
    &__nav li a:active {
        color: #919720;
    }

    &__links li a:hover,
    &__links li a:active {
        font-size: 160%;
    }
}

.facebook,
.twitter,
.google,
.instagram {
    transition: color 0.2s;
}

.facebook:hover {
    color: #3b5998;
}

.twitter:hover {
    color: #00aced;
}

.google:hover {
    color: #dd4b39;
}

.instagram:hover {
    color: #517fa4;
}

footer p {
    color: #d8d174;
    text-align: center;
}
